body {
  margin: 0;
  padding: 0; }
  body .spotim-header {
    text-align: center;
    background-color: #0082DD;
    height: 150px;
    padding: 20px;
    color: white; }
    body .spotim-header .spotim-title {
      font-size: 1.5em; }
  body .spotim-body {
    margin-top: 2px;
    margin-bottom: 2px; }
    body .spotim-body .spotim-chat-header {
      text-align: center; }
    body .spotim-body .spotim-chat-body {
      height: 300px;
      overflow: auto;
      margin-top: 5px;
      margin-bottom: 5px; }
    body .spotim-body .spotim-chat-footer {
      margin-bottom: 5px; }
  body .spotim-footer {
    bottom: 0;
    margin: 0px;
    padding: 5px;
    text-align: center;
    background-color: #0082DD;
    color: white; }
  body .avatar {
    vertical-align: middle;
    width: 25 px;
    height: 25px;
    border: 1px solid green;
    padding: 1px;
    border-radius: 50% 15%; }
  body .avatar-user {
    vertical-align: middle;
    width: 25 px;
    height: 25px;
    border: 1px solid blue;
    padding: 1px;
    border-radius: 50% 15%; }



